@media print {
  html {
    background-color: #fff; }
  body,
  body.has-background-image,
  body:not(.has-background-image) {
    background-image: none;
    background-color: #fff;
    min-height: 100%; }
  a,
  .sup-header-default a {
    text-decoration: underline;
    color: #333; }
  main {
    width: 100% !important;
    margin-left: 0; }
  .site-footer {
    display: none; } }

body {
  font-size: 18px; }

p {
  font-size: 18px;
  font-size: 1em;
  line-height: 1.8; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1;
  margin-bottom: 0; }

h1 {
  font-size: 68.66455px; }

h2 {
  font-size: 54.93164px; }

h3 {
  font-size: 43.94531px; }

h4 {
  font-size: 35.15625px; }

h5 {
  font-size: 28.125px; }

h6 {
  font-size: 22.5px; }

.site-header {
  background-color: #2ec1b4;
  padding: 20px; }

.site-header-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row;
          flex-flow: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  max-width: 1200px;
  margin: 0 auto; }

.site-footer {
  background-color: #2ec1b4;
  padding: 20px; }

.site-info {
  max-width: 1200px;
  margin: 0 auto; }

.site-content.fixed-width {
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto; }

.page-template-page-sidebar .site-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.page-template-page-sidebar .content-area {
  width: 70%; }

.page-template-page-sidebar .widget-area {
  width: 30%; }

.has-black-background-color {
  background-color: #232323; }

.has-white-color {
  color: #ffffff; }

.alignfull {
  margin-left: -100%;
  margin-right: -100%; }

.alignwide {
  margin-left: -5%;
  margin-right: -5%; }

.alignfull .wp-block-group__inner-container,
.alignwide .wp-block-group__inner-container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto; }

.p-back-to-top_container {
  position: fixed;
  bottom: 20px;
  right: 20px; }

.p-back-to-top_link {
  display: block;
  background-color: #fff;
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
  border-radius: 100%;
  width: 50px;
  height: 50px;
  padding: 10px;
  color: white;
  border: 3px solid black; }

.p-back-to-top_link:hover {
  background-color: whitesmoke; }
